  background-color: #E5E5E5;
  font-family: 'PT Sans', sans-serif;
}



.top{
  margin-top: 2.5em;
  margin-bottom: 2.5em;
}
.todayDate{
  font-size: 2.5em;
}

.calendar{
  margin-right: .5em;
}

.App{
  /*display: 1;*/
  background-color: #E5E5E5;
  border-color: #000;
  border-style: solid;
  border-width: thin;
  border-radius: 10px;
  min-height:500px;
  max-width: 450px;
}

.top .App{
  /*display: 1;*/
  background-color: #E5E5E5;
  border-color: #000;
  border-style: solid;
  border-width: thin;
  border-radius: 10px;
  min-height:250px;
  max-width: 450px;
}

#to-do-form input{
  background-color: #E5E5E5;
  border-color: #000;
  border-style: solid;
  border-width: thin;
  border-radius: 5px;
  color: #4E4E4E;
  /*width: 200px;*/
  height: 40px;
  margin: 1em;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::-webkit-input-placeholder{
  color: #9F9F9F;
}
#to-do-form input::-moz-placeholder{
  color: #9F9F9F;
}
#to-do-form input:-ms-input-placeholder{
  color: #9F9F9F;
}
#to-do-form input::-ms-input-placeholder{
  color: #9F9F9F;
}
#to-do-form input::placeholder{
  color: #9F9F9F;
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 40px;
  max-width: 11em;
  margin: 1em;
  padding-left: .75em;
  padding-right: .75em;
  background-color: #E5E5E5;
  border-color: #000;
  border-style: solid;
  border-width: thin;
  border-radius: 5px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
}
#to-do-form button:focus{
  outline: none;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}
